import React, { Component } from 'react'
import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/theme/monokai.css';
import Directory from "../../Directory";

let code1 = `<div id="myDiagramDiv" style="height: 400px;width: 400px;border: 1px solid #000;"></div>

let $ = go.GraphObject.make;
let myDiagram = $(go.Diagram, "myDiagramDiv");`;

export default class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            articleTree: []
        };
        this.getArticleTree = this.getArticleTree.bind(this);
    }
    getArticleTree() {
        let childrens = document.getElementsByClassName("article-content")[0].children;
        let articleTree = [];
        for (let i = 0; i < childrens.length - 1; i++) {
            let nodeName = childrens[i].nodeName;
            if (nodeName === "H2" || nodeName === "H3") {
                childrens[i].id = childrens[i].innerText;
                articleTree.push({
                    name: childrens[i].innerText,
                    tag: childrens[i].nodeName
                });
            }
        }
        this.setState({ articleTree });
    }
    componentDidMount() {
        this.getArticleTree();
    }
    render() {
        return (
            <div className="page-content">
                <div className="article">
                    <div className="article-title">
                        <h1>GoJS入门教程——第二节 GoJS应用中有什么</h1>
                    </div>
                    <div className="article-content">
                        <h2>模型-视图体系结构</h2>
                        <p><em>“视图”——将模型中的节点和链接数据可视化到GoJS节点/链接对象中</em></p>
                        <ul>
                            <li>Diagram保存并显示部件
                            <ul>
                                    <li>部分是节点、链接、组和装饰</li>
                                </ul>
                            </li>
                            <li>Model存储描述图表部分的数据</li>
                        </ul>
                        <p>如图所示：</p>
                        <div className="text-center">
                            <img src={require("../../../assets/images/1585126990498-9287a82e-b7bd-4a6c-85e0-8805577ef2ce.png")} style={{ width: "300px" }} alt="" />
                        </div>
                        <h2>构建一个Diagram</h2>
                        <CodeMirror
                            value={code1}
                            options={{
                                theme: 'monokai',
                                mode: 'JSX',
                                readOnly: true
                            }}
                        />
                        <p>go.GraphObject.make(go.Diagram，div元素或div ID)</p>
                        <h2>构建一个Model</h2>
                        <ul>
                            <li>存储稍后将在其关系图中显示的数据</li>
                        </ul>
                        <ul data-lake-indent="1">
                            <li>nodeDataArray存储节点数据</li>
                            <li>linkDataArray存储链接数据</li>
                        </ul>
                        <ul>
                            <li>go.GraphObject.make(go.Model)</li>
                        </ul>
                        <p><em>工具和命令处理工具默认存在，例如：你可以使用delete键删除元素，Ctrl-C、Ctrl-V复制粘贴元素</em></p>
                        <p><em>更多详情请看<a href="https://gojs.net/latest/intro/extensions.html">GoJS_Intro_Extensions</a></em></p>
                        <h2>Model的种类</h2>
                        <p>GoJS Model可以是3种类型中的一种</p>
                        <ul>
                            <li>Model(默认)</li>
                            <li>GraphLinksModel</li>
                            <li>TreeModel</li>
                        </ul>
                        <h2>Layouts</h2>
                        <ul>
                            <li>可以应用于图表或组</li>
                            <li>排列其元素的节点</li>
                        </ul>
                        <p><em>更多详情请看<a href="https://gojs.net/latest/intro/layouts.html">GoJS_Intro_Layouts</a></em></p>
                        <h2>总结</h2>
                        <ul>
                            <li>GoJS有一个model-view架构</li>
                        </ul>
                        <ul data-lake-indent="1">
                            <li>Diagram ≈ View</li>
                            <li>Diagram.model ≈ Model</li>
                        </ul>
                        <ul>
                            <li>GoJS交互由Tools和CommandHandler控制</li>
                            <li>Layouts可以应用于Diagram或Groups来安排它们的内容自适应</li>
                        </ul>
                        <p><br /></p>
                        <p><br /></p>
                        <p>相关代码：</p>
                        <p><a href="https://github.com/LiuZheAb/GoJS_Demo/blob/master/02whatIsInGojsApp.html">https://github.com/LiuZheAb/GoJS_Demo/blob/master/02whatIsInGojsApp.html</a></p>
                    </div>
                </div>
                <div className="directory">
                    <Directory articleTree={this.state.articleTree}></Directory>
                </div>
            </div>
        )
    }
}
